import { Component, h, Element, Event, EventEmitter, Prop } from '@stencil/core';
import tailwind from '../../output.css';
import { data } from '../../data';
@Component({
  tag: 'rf-modal',
  shadow: true,
})
export class Modal {
  @Element() element: HTMLElement;

  rooms = data.My_Result.roomtypes;
  amenities = data.My_Result.amenities;

  roomAmenities = this.amenities.filter(r => {
    return r.amenity_type == 'room';
  });

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }
  @Prop() roomId: number;

  @Event({ bubbles: true, composed: true }) rf_hide: EventEmitter<boolean>;
  hide(){
    this.rf_hide.emit(false)
  }
  render() {
    return (
      <div class="fixed inset-0 flex justify-center items-center z-50">
        <div class="absolute inset-0 bg-gray-800 opacity-75" onClick={this.hide.bind(this)}></div>

        <div class="relative bg-white w-full lg:w-3/4 xl:w-1/2 p-6 rounded-lg md:h-3/4 overflow-y-scroll shadow-lg z-10 flex flex-col gap-2">
          <div class='flex flex-row justify-between'>
            <h1 class="text-2xl font-semibold">{this.rooms[this.roomId].name}</h1>
            <button class='rounded-full hover:bg-gray-200 text-gray-500 font-semibold px-2' onClick={this.hide.bind(this)}>X</button>
          </div>
          <div class="hidden md:block w-3/4 h-3/4 m-auto">
            <rf-carousel
              images={this.rooms[this.roomId].images.map(i => {
                return i.url;
              })}
            ></rf-carousel>
          </div>
          <div class="flex flex-row gap-4 text-sm justify-start font-semibold">
            <div>
              {this.rooms[this.roomId].size}m<sup>2</sup>
            </div>
            <div>{this.amenities[25].description}</div>
            <div class="flex flex-row gap-x-2">
              {this.rooms[this.roomId].bedding_setup.map(b => {
                return <div>{b.name}</div>;
              })}
            </div>
          </div>
          <div class="text-sm">{this.rooms[this.roomId].description}</div>
          <h3 class="text-lg font-semibold">Amenities</h3>
          <div class="grid grid-cols-3 gap-y-1">
            {this.roomAmenities.map(e => {
              return <div class="text-sm">{e.description}</div>;
            })}
          </div>
        </div>
      </div>
    );
  }
}
